<template>
	<a :href="link">
		<i :class="icon_class"></i>
	</a>
</template>

<script>
	export default {
		name: "SocialShare",
		props: {
			icon: {
				type: String,
				required: true
			},
			link: {
				type: String,
				required: true
			}
		},
		computed: {
			icon_class: function() {
				return `fab ${this.icon}`;
			}
		}
	}
</script>