<template>
	<li>
		<i :class="icon_class"></i>
		<a :href="link">{{ label }}</a>
	</li>
</template>

<script>
	export default {
		name: "MenuItem",
		props: {
			iconPrefix: String,
			icon: {
				type: String,
				required: true
			},
			label: {
				type: String,
				required: true
			},
			link: {
				type: String,
				required: true
			}
		},
		computed: {
			icon_class: function() {
				return `${this.iconPrefix || "fas"} ${this.icon}`;
			}
		}
	}
</script>