<template>
    <div id="app">
        <menu-toggle @open="handleMenuOpening" :opened="menuOpenState"></menu-toggle>
        <nav :class="opener">
            <brand brand-name="WebDev - Ebalo" @close="handleMenuClosing"></brand>
            <ul>
                <menu-item v-for="item of links" :icon="item.icon" :label="item.label"
                    :link="item.link" :icon-prefix="item.hasOwnProperty('prefix') ? item.prefix : null"
                    :key="item.label"></menu-item>
            </ul>
            <div class="share">
                <social-share v-for="item of socialShare" :icon="item.icon" :link="item.link"
                    :key="item.icon"></social-share>
            </div>
        </nav>
        <section :class="opener">
            <img src="assets/background.jpg"/>
        </section>
    </div>
</template>

<script>
import MenuToggle from "@/components/MenuToggle";
import Brand from "@/components/Brand";
import MenuItem from "@/components/MenuItem";
import SocialShare from "@/components/SocialShare";

export default {
    name: 'App',
    components: {SocialShare, MenuItem, Brand, MenuToggle},
    data: function() {
        return {
            menuOpenState: false,
            links: [
                { icon: "fa-home", label: "Home", link: "#" },
                { icon: "fa-user", label: "Account", link: "#" },
                { icon: "fa-lock", label: "Security", link: "#" },
                { icon: "fa-heart", label: "Likes", link: "#" },
                { icon: "fa-comment", label: "Comments", link: "#" },
                { icon: "fa-bookmark", label: "Saves", link: "#" },
                { icon: "fa-question", label: "About", link: "#" },
            ],
            socialShare: [
                { icon: "fa-instagram", link: "#" },
                { icon: "fa-twitter", link: "#" },
                { icon: "fa-facebook", link: "#" },
                { icon: "fa-linkedin-in", link: "#" },
            ]
        };
    },
    methods: {
        handleMenuOpening: function() {
            this.menuOpenState = true;
        },
        handleMenuClosing: function() {
            this.menuOpenState = false;
        }
    },
    computed: {
        opener: function() {
            return this.menuOpenState ? "show" : "";
        }
    },
}
</script>
